import { getAllCandiesAction, getAvailableYearsAction } from "@/app/actions";
import { AddCandyFormWrapper } from "@/components/add-candy-form-wrapper";
import { CandyInsights } from "@/components/candy-insights";
import { CandyList } from "@/components/candy-list";
import { YearSelectorWrapper } from "@/components/year-selector-wrapper";
import { getCurrentYear } from "@/lib/candy";

interface HomeProps {
  searchParams: { year?: string };
}

export default async function Home({ searchParams }: HomeProps) {
  const yearParam = searchParams.year ? Number(searchParams.year) : undefined;
  const selectedYear = yearParam && !isNaN(yearParam) ? yearParam : getCurrentYear();
  
  const candies = await getAllCandiesAction(selectedYear);
  const availableYears = await getAvailableYearsAction();

  return (
    <div className="container mx-auto max-w-4xl py-8 px-4 sm:px-6 lg:px-8">
      <div className="space-y-8">
        <header className="text-center space-y-2">
          <h1 className="text-4xl font-bold tracking-tight">Candy Counter</h1>
          <p className="text-muted-foreground">
            Track your trick-or-treat candy collection
          </p>
        </header>

        <div className="rounded-lg border bg-card p-6 shadow-sm">
          <div className="flex items-center justify-between mb-4">
            <h2 className="text-xl font-semibold">Year</h2>
            <YearSelectorWrapper 
              selectedYear={selectedYear} 
              availableYears={availableYears}
            />
          </div>
        </div>

        <div className="rounded-lg border bg-card p-6 shadow-sm">
          <h2 className="text-xl font-semibold mb-4">Insights</h2>
          <CandyInsights candies={candies} />
        </div>

        <div className="rounded-lg border bg-card p-6 shadow-sm">
          <h2 className="text-xl font-semibold mb-4">Add Candy</h2>
          <AddCandyFormWrapper />
        </div>

        <div className="rounded-lg border bg-card p-6 shadow-sm">
          <h2 className="text-xl font-semibold mb-4">Your Candy Collection</h2>
          <CandyList initialCandies={candies} />
        </div>
      </div>
    </div>
  );
}
